import React from 'react';
import moment from 'moment';
import { InputNumber, Button } from 'antd';

export const columns1 = [
  {
    name: 'id',
    formItem: {
      type: 'hidden'
    }
  },
  {
    title: '',
    name: 'roleType',
    dict: [
      { code: '1', codeName: '111' },
      { code: '2', codeName: '222' },
      { code: '3', codeName: '333' }
    ],
    formItem: {
      type: 'select'
    }
  },
  {
    title: '',
    name: 'roleName',
    formItem: {}
  },
  {
    title: '',
    name: 'ordder',
    formItem: {
      type: 'number'
    }
  }
];

export const columns2 = [
  {
    name: 'id',
    formItem: {
      type: 'hidden'
    }
  },
  {
    title: '',
    name: 'roleType',
    dict: [
      { code: '1', codeName: '111' },
      { code: '2', codeName: '222' },
      { code: '3', codeName: '333' }
    ],
    formItem: {
      type: 'select'
    }
  },
  {
    title: '',
    name: 'roleName',
    formItem: {
      initialValue: ''
    }
  }
];

export const columns3 = [
  {
    name: 'id',
    formItem: {
      type: 'hidden'
    }
  },
  {
    title: '',
    name: 'roleType',
    dict: [
      { code: '1', codeName: '' },
      { code: '2', codeName: '' },
      { code: '3', codeName: '' }
    ],
    formItem: {
      type: 'select',
      rules: [{ required: true, message: '' }]
    }
  },
  {
    title: '',
    name: 'roleName',
    formItem: {
      rules: [
        {
          required: true,
          message: ''
        },
        {
          pattern: /^[\w\u4E00-\u9FA5()]{1,20}$/,
          message: '1-20'
        }
      ]
    }
  }
];

export const columns4 = [
  {
    title: '',
    name: 'user_name',
    formItem: {
      rules: [
        {
          required: true,
          message: ''
        }
      ]
    }
  },
  {
    title: '',
    name: 'user_password',
    formItem: {
      type: 'password'
    }
  }
];

export const columns5 = [
  {
    title: '',
    name: 'user_name',
    formItem: {
      rules: [
        {
          required: true,
          message: ''
        }
      ]
    }
  },
  {
    title: '',
    name: 'user_password',
    formItem: {
      type: 'password',
      repeat: true
    }
  }
];

export const columns6 = [
  {
    title: '',
    name: 'date',
    formItem: {
      type: 'date'
    }
  },
  {
    title: '()',
    name: 'datetime',
    formItem: {
      type: 'datetime',
      showTime: true,
      initialValue: moment()
    }
  },
  {
    title: '',
    name: 'rangedate',
    formItem: {
      type: 'date~'
    }
  },
  {
    title: '()',
    name: 'rangedate2',
    formItem: {
      type: 'date~',
      showTime: true
    }
  },
  {
    title: '',
    name: 'time',
    formItem: {
      type: 'time'
    }
  }
];

export const columns7 = [
  {
    title: '',
    name: 'date',
    formItem: {
      type: 'date'
    }
  },
  {
    title: '()',
    name: 'datetime',
    formItem: {
      type: 'datetime',
      col: { span: 12 },
      formItemLayout: {
        labelCol: { span: 12 },
        wrapperCol: { span: 12 }
      },
      showTime: true,
      initialValue: moment()
    }
  },
  {
    title: '',
    name: 'rangedate',
    formItem: {
      col: { span: 12 },
      formItemLayout: {
        labelCol: { span: 6 },
        wrapperCol: { span: 16 }
      },
      type: 'date~'
    }
  },
  {
    title: '()',
    name: 'rangedate2',
    formItem: {
      type: 'date~',
      showTime: true
    }
  },
  {
    title: '',
    name: 'time',
    formItem: {
      type: 'time'
    }
  }
];

export const columns8 = [
  {
    name: 'id',
    formItem: {
      type: 'hidden'
    }
  },
  {
    title: '',
    name: 'color',
    formItem: {
      type: 'transfer',
      initialValue: [1, 3],
      dataSource: [
        { key: 1, title: 'red' },
        { key: 2, title: 'yellow' },
        { key: 3, title: 'blue' },
        { key: 4, title: 'green' }
      ],
      onChange: (form, value) => console.log('---:', value),
      listStyle: {
        width: 114
      },
      rules: [{ required: true, message: '' }]
    }
  },
  {
    title: '',
    name: 'popup-color',
    formItem: {
      type: 'transfer',
      modal: {
        // modal 
      },
      dataSource: [
        { key: 1, title: 'red' },
        { key: 2, title: 'yellow' },
        { key: 3, title: 'blue' },
        { key: 4, title: 'green' }
      ],
      onChange: (form, value) => console.log(':', value),
      rules: [{ required: true, message: '' }]
    }
  }
];

export const columns9 = [
  {
    title: '',
    name: 'user_name',
    formItem: {}
  },
  {
    title: '',
    formItem: {
      type: 'custom',
      render: (record, form) => {
        const { getFieldDecorator } = form;
        return getFieldDecorator('age', {
          initialValue: record && record.age
        })(
          <div>
            <InputNumber />
            <Button size='small'></Button>
          </div>
        );
      }
    }
  }
];



